export default {
  install: function (Vue, opt) {
    Vue.mixin({
      data () {
        return {
          show: false,
          show1: false,
          chosen: '', // radio
          isCheck: true // switch
        }
      },
      methods: {
        test () {
          console.log('')
          this.$router.push('button')
        },
        back () {
          this.$router.go(-1)
        },
        // radio
        input (data) {
          console.log('radio', data)
        }
      }
    })
  }
}



