<template>
  <div>
    <cell class="radio"
      v-for="(item, index) in options"
      :for="`radio-${index}`"
      :key="index"
      :title="getValue(item)"
      :class="{
        'is-checked': getKey(item) == currentValue,
        'is-circle': circle
      }"
    >
      <slot slot="icon">
        <img :src="item.icon" alt="" v-if="typeof item === 'object' && item.icon && item.icon.substr(0,4) !== 'icon'">
        <i class="iconfont" :class="item.icon" v-if="typeof item === 'object' && item.icon && item.icon.substr(0,4) == 'icon'"></i>
      </slot>
      <input type="radio"
        :id="`radio-${index}`"
        v-model="currentValue"
        :value="getKey(item)"
      >
      <i class="iconfont icon-check"></i>
    </cell>
  </div>
</template>

<script>
import {getKey, getValue, getValueByKey} from '../util'

export default {
  name: 'Radio',
  filters: {
    getValue,
    getKey
  },
  props: {
    options: Array, // 
    value: String, // key
    circle: Boolean // 
  },
  data () {
    return {
      currentValue: this.value // key
    }
  },
  methods: {
    getValue,
    getKey
  },
  watch: {
    currentValue (newVal) {
      // key
      this.$emit('input', newVal)
      // keyvalue
      this.$emit('change', newVal, getValueByKey(this.options, newVal))
    }
  }
}
</script>

<style lang="sass">
@import  "../theme/radio.scss";
</style>



