<template>
  <div>
    <cell class="checkbox"
      v-for="(item, index) in options"
      :for="`checkbox-${index}`"
      :key="index"
      :title="getValue(item)"
    >
      <input type="checkbox">
    </cell>
  </div>
</template>

<script>
import {getKey, getValue} from '../util'

export default {
  name: 'CheckList',
  filters: {
    getValue,
    getKey
  },
  props: {
    options: {
      type: Array,
      required: true
    }
  },
  methods: {
    getValue,
    getKey
  }
}
</script>

<style lang="sass">
@import  "../theme/checklist.scss";
</style>



